body {
    background: linear-gradient(27deg, rgba(63,180,251,1) 19%, rgba(70,70,252,1) 85%);
    font-family: Noto Sans, Helvetica, sans-serif;
    color: #666;
    letter-spacing: 1px;
    padding: 10px;
    min-height: 100vh;
}

.btn-act {
    padding: 1px;
}

.table-responsive {
    margin: 30px 0;
}

.table-wrapper {
    background: #fff;
    padding: 20px 25px;
    border-radius: 3px;
    min-width: 1000px;
    box-shadow: 0 1px 1px rgba(0,0,0,.05);
}

.table-title {
    padding-bottom: 15px;
    background: #4169e1;
    color: #fff;
    padding: 16px 16px;
    min-width: 70%;
    margin: auto;
    border-radius: 3px 3px 0 0;
    vertical-align: middle;
}

.table-title h2 {
    margin: 1px 0 0;
    font-size: 24px;
}

.table-title .btn-group {
    float: right;
}

.table-title .btn {
    color: #fff;
    float: right;
    font-size: 13px;
    border: none;
    min-width: 50px;
    border-radius: 2px;
    border: none;
    outline: none !important;
    margin-left: 10px;
}

.table-title .btn i {
    float: left;
    font-size: 21px;
    margin-right: 5px;
}

.table-title .btn span {
    float: left;
    margin-top: 2px;
}

table.table tr th, table.table tr td {
    border-color: #e9e9e9;
    font-size: 14px;
    padding: 10px 13px;
    vertical-align: middle;
    text-align: center;
    min-width: 100%;
}

table.table tr th:last-child {
    width: 210px;
}

table.table-striped tbody tr:nth-of-type(odd) {
    background-color: #fcfcfc;
}

table.table-striped.table-hover tbody tr:hover {
    background: #f5f5f5;
}

table.table th i {
    font-size: 11px;
    margin: 0 5px;
    cursor: pointer;
}

table.table td:last-child i {
    opacity: 0.9;
    font-size: 18px;
    margin: 0 5px;
}

table.table td a {
    font-weight: bold;
    color: #566787;
    display: inline-block;
    text-decoration: none;
    outline: none !important;
}

table.table td a:hover {
    color: #2196F3;
}

table.table td a.edit {
    color: #FFC107;
}

table.table td a.delete {
    color: #F44336;
}

table.table td i {
    font-size: 45px;
}

table.table .avatar {
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 10px;
}

.pagination {
    float: right;
    margin: 0 0 5px;
}

.pagination li a {
    border: none;
    font-size: 11px;
    min-width: 30px;
    min-height: 30px;
    color: #999;
    margin: 0 2px;
    line-height: 30px;
    border-radius: 2px !important;
    text-align: center;
    padding: 0 6px;
}

.pagination li a:hover {
    color: #666;
}

.pagination li.active a, .pagination li.active a.page-link {
    background: #03A9F4;
}

.pagination li.active a:hover {
    background: #0397d6;
}

.pagination li.disabled i {
    color: #ccc;
}

.pagination li i {
    font-size: 13px;
    padding-top: 6px
}

.hint-text {
    float: left;
    margin-top: 10px;
    font-size: 11px;
}

/* Modal styles */
.modal .modal-dialog {
    max-width: 400px;
}

.modal .modal-header, .modal .modal-body, .modal .modal-footer {
    padding: 20px 30px;
}

.modal .modal-content {
    border-radius: 3px;
    font-size: 14px;
}

.modal .modal-footer {
    background: #ecf0f1;
    border-radius: 0 0 3px 3px;
}

.modal .modal-title {
    display: inline-block;
}

.modal .form-control {
    border-radius: 2px;
    box-shadow: none;
    border-color: #dddddd;
}

.modal textarea.form-control {
    resize: vertical;
}

.modal .btn {
    border-radius: 2px;
    min-width: 100px;
}

.modal form label {
    font-weight: normal;
}

.error {
    color: #D8000C;
    background: #ffffff;
    padding: 7px;
    border: dashed;
    font-size: 13px;
    margin-top: 7px;
}

.errorMsg {
    color: #D8000C;
    background: #ffffff;
    padding: 7px;
    text-align: center;
    border: double;
    font-size: 13px;
}

.input-control {
    border: 1px solid #ccc;
    color: #555555;
    background-color: #fff;
    height: 46px;
    font-size: 18px;
    line-height: 1.3333333;
    border-radius: 0;
    display: block;
    margin-bottom: 30px;
    padding: 10px;
    width: 100%;
}

header {
    text-align: center;
    margin: 10px 0 10px 0;

    .links {
        text-transform: uppercase;
        text-align: center;
        font-size: 150%;
        margin-bottom: 20px;

        .link:link,
        .link:visited {
            padding: 5px 10px;
            font-weight: 100;
            margin: 1rem;
            color: #fff;
            text-decoration: none;
            text-transform: uppercase;
            border-bottom: 2px solid transparent;
            transition: all 0.3s;
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -ms-transition: all 0.3s;
            -o-transition: all 0.3s;

            &.active {
                font-weight: 500;
            }
        }

        .link:hover,
        .link:active {
            border-bottom: 2px solid #fff;
        }
    }
}

h1 {
    color: #b6ff00;
    display: inline-block;
}

h1,
h2 {
    color: #fff;
}

@media screen and (max-width: 400px) {
    h1 {
        font-size: 30px;
    }

    header {
        .links {
            a {
                font-size: 1.2rem;
            }

            .link:link,
            .link:visited {
                margin: unset;
            }
        }
    }

    .main-form {
        width: 100%;
    }
}

